const codeString = `
/*
  Method 1: Recursive
  Time complexity O(2^n) 
  Space complexity O(n)
*/
const getFibonacciRecursive = (num: number): number => {
    if (num === 0 || num === 1) return num; // 不要寫成 if (num === 0 || 1) return num;
    return getFibonacciRecursive(num - 1) + getFibonacciRecursive(num - 2);
};

/*
  Method 2: Iterative 
  Time complexity O(n) 
  Space complexity O(1)
*/
const getFibonacciIterative = (num: number): number => {
  if (num <= 1) return num;

  let previous = 0, current = 1;
  let next = 0;

  for (let i = 2; i <= num; i++) {
    next = previous + current;
    previous = current;
    current = next;
  }

  return current;

/*
  Method 3: Memoization (Dynamic Programming: Top-Down)
  Time complexity O(n)
  Space complexity O(n)
*/
const getFibonacciMemoization = (num: number): number => {
    // const memo: number[] = Array.from({ length: num + 1 }, () => -1);
  const memo: number[] = Array(num + 1).fill(-1);
  const helper = (n: number): number => {
    if (n === 0 || n === 1) return n;
    if (memo[n] !== -1) return memo[n];
    memo[n] = helper(n - 1) + helper(n - 2);
    return memo[n];
  };
  return helper(num);
};
`;

